.search-overlay {
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 110;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.96);
    visibility: hidden;
    opacity: 0;
    transform: scale(1.09);
    transition: opacity 0.3s, transform 0.3s, visibility 0.3s;
    box-sizing: border-box;
  
    p {
      padding-top: 15px;
    }
  
    .event-summary p {
      padding-top: 0;
    }
  
    .event-summary {
      margin-bottom: 0;
      margin-top: 20px;
    }
  
    body.admin-bar & {
      top: 32px;
    }
  
    &__top {
      background-color: rgba(0, 0, 0, 0.12);
    }
  
    &__icon {
      margin-right: 10px;
      font-size: 1.8rem;
      color: $green--light;
      @include atMedium {
        font-size: 2.5rem;
      }
    }
  
    &--active {
      visibility: visible;
      opacity: 1;
      transform: scale(1);
    }
  
    &__section-title {
      margin: 30px 0 1px 0;
      font-weight: 400;
      color: $green--light;
      font-size: 2rem;
      padding: 15px 0;
      border-bottom: 1px solid #ccc;
    }
  
    &__close {
      position: absolute;
      top: 13px;
      right: 16px;
      font-size: 2.1rem;
      cursor: pointer;
      transition: all 0.3s;
      background-color: #fff;
      color: $green--light;
      line-height: 0.7;
      @include atSmall {
        top: 18px;
        font-size: 2.1rem;
      }
      @include atMedium {
        top: 26px;
        font-size: 2.8rem;
      }
    }
  
    &__close:hover {
      opacity: 1;
    }
  
    .one-half {
      padding-bottom: 0;
    }
  }
  
  .search-term {
    width: 75%;
    box-sizing: border-box;
    border: none;
    padding: 15px 0;
    margin: 0;
    background-color: transparent;
    font-size: 1rem;
    font-weight: 300;
    outline: none;
    color: $green--light;
    @include atSmall {
      font-size: 1.5rem;
    }
    @include atMedium {
      width: 80%;
      font-size: 3rem;
    }
  }
  
  .body-no-scroll {
    overflow: hidden;
  }
  